<template>
  <div class="index-box">
  
   <main class="main main--home">
     <div class="mint-box">
       <div class="top-button">
            <el-button style="width: 240px; height: 60px; font-size: 20px; font-weight: bold" type="primary" size="large">MINT</el-button>
       </div>
       <div class="title">Silky Finance</div>
       <div class="info-nox">
         <div class="info-title">Phase 1:Mint Free $SKY Now!</div>
         <div class="desc">SKY NFT holders participate in the current castable amount of<br>300,000,000 (total supply 30%)</div>
        <div class="btn-box flex-between">
            <el-button style="width: 31%; height: 60px; font-size: 20px; font-weight: bold" type="primary" size="large" :loading="mintLoading" @click="mintNft">START MINT</el-button>
            <el-button style="width: 31%; height: 60px; font-size: 20px; font-weight: bold;" type="success" size="large" :loading="claimLoading" @click="withdrawReward">CLAIM</el-button>
            <el-button style="width: 31%; height: 60px; font-size: 20px; font-weight: bold" type="success" size="large">BUY SKY</el-button>
        </div>
        <div class="income-box flex-between">
          <div class="item">Real Time Benefit: <span>{{reward}}</span> SKY</div>
          <div class="item">Current Rate <span>10</span> (SKY/hr)</div>
        </div>
        <div class="my-nft-box">
          <div class="nft-title">MY NFT</div>
          <div class="nft">
            <div class="nft-item flex-middle" v-for="item in nftList" :key="item">
               <div class="item-title flex-item">#{{ item }}</div>
               <div class="item-btns">
                 <el-button v-if="isApprove" type="primary" size="large" :loading="stakeLoading[item]" @click="stake(item)">STAKE</el-button>
                 <el-button v-else type="primary" size="large" @click="approveNft">APPROVE NFT</el-button>
                  
               </div>
            </div>
            <div class="nft-item flex-middle" v-for="item in stakeList" :key="item">
               <div class="item-title flex-item">#{{ item }}</div>
               <div class="item-btns">
                  <el-button type="success" size="large" :loading="unStakeLoading[item]" @click="unStake(item)">UNSTAKE</el-button>
               </div>
            </div>
          </div>
        </div>
       </div>
     </div>
   </main>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
import { ElMessage } from 'element-plus';
import nftAbi from "@/abi/nftAbi"
import stakeAbi from "@/abi/stakeAbi"
import config from "@/utils/config"
import BigNumber from "bignumber.js"

export default {
  components: {
  },
  setup () {
	 const store = useStore()
     const data = reactive({
	     address: computed(() => store.state.account),
       web3: computed(() => store.state.provider),
       currentBalance: computed(() => store.state.currentBalance),
       nftContract: null,
       stakeContract: null,
       mintLoading: false,
       stakeLoading: {},
       unStakeLoading: {},
       claimLoading: false,
       isApprove: false,
        nftList: [],
        stakeList: [],
        reward: '0.000',
        initContract: () => {
          console.log(config.stakeAddress, ' config.stakeAddres')
        data.nftContract = new data.web3.eth.Contract(nftAbi, config.nftAddress)
        data.stakeContract = new data.web3.eth.Contract(stakeAbi, config.stakeAddress)
        data.initData()
      },
       initData: () => {
         data.getNfts()
         data.getIsApprove()
         data.getReward()
       },
       getNfts: async () => {
         let nfts = await data.nftContract.methods.getUserAllTokens(data.address).call()
         console.log(nfts)
         let stakes = await data.stakeContract.methods.getUserStakeIds(data.address).call()
         console.log(stakes, 'stakes')
         data.nftList = nfts
         data.stakeList = stakes
       },
       getReward: async () => {
         let reward = await data.stakeContract.methods.getReward(data.address).call()
         console.log(reward)
         data.reward = new BigNumber(reward).dividedBy(10**18).toFixed(3)
       },
       getIsApprove: async () => {
         let isApprove = await data.nftContract.methods.isApprovedForAll(data.address, config.stakeAddress).call()
         data.isApprove = isApprove
       },
       approveNft: async () => {
          let gas = await data.nftContract.methods.setApprovalForAll(config.stakeAddress, true).estimateGas({from: data.address})
          await data.nftContract.methods.setApprovalForAll(config.stakeAddress, true).send({ from: data.address, gas: gas });
          data.initData()
       },
       mintNft: async () => {
         let isMint = await data.nftContract.methods.isMint(data.address).call()
         if(isMint) {
           ElMessage.error("Already Mint")
           return
         }
         data.mintLoading = true;
         try {
           let gas = await data.nftContract.methods.mint().estimateGas({from: data.address})
          await data.nftContract.methods.mint().send({ from: data.address, gas: gas });
          data.mintLoading = false
          ElMessage.success("Mint Success")
          data.initData()
         } catch (error) {
          data.mintLoading = false
          ElMessage.error("Mint Fail")
           console.log(error)
         }
       },
       stake: async (tokenId) => {

         let ethAmount = await data.stakeContract.methods.mintFee().call()
      
         if(Number(data.currentBalance) <= Number(ethAmount/10**18)) {
           ElMessage.error("Insufficient ETH balance")
           return
         }

         data.stakeLoading[tokenId] = true;
         try {
           let gas = await data.stakeContract.methods.stakeNft(tokenId).estimateGas({from: data.address, value: ethAmount })
          console.log(gas, 'gas---')
          await data.stakeContract.methods.stakeNft(tokenId).send({ from: data.address, gas: gas, value: ethAmount });
           data.stakeLoading[tokenId] = false
          ElMessage.success("Stake Success")
          data.initData()
         } catch (error) {
           data.stakeLoading[tokenId] = false
          ElMessage.error("Stake Fail")
           console.log(error)
         }
       },
       unStake: async (tokenId) => {
      
         data.unStakeLoading[tokenId] = true;
         try {
           let gas = await data.stakeContract.methods.unStake(tokenId).estimateGas({from: data.address })
          console.log(gas, 'gas---')
          await data.stakeContract.methods.unStake(tokenId).send({ from: data.address, gas: gas });
           data.unStakeLoading[tokenId] = false
          ElMessage.success("unStake Success")
          data.initData()
         } catch (error) {
           data.unStakeLoading[tokenId] = false
          ElMessage.error("unStake Fail")
           console.log(error)
         }
       },
        withdrawReward: async () => {
      
         data.claimLoading = true;
         try {
           let gas = await data.stakeContract.methods.withdrawReward().estimateGas({from: data.address })
          console.log(gas, 'gas---')
          await data.stakeContract.methods.withdrawReward().send({ from: data.address, gas: gas });
           data.claimLoading = false
          ElMessage.success("Claim Success")
          data.initData()
         } catch (error) {
           data.claimLoading = false
          ElMessage.error("Claim Fail")
           console.log(error)
         }
       }
     })
      if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

<style lang="scss" scoped>
.main{
  min-height: calc(100vh - 284px);
  padding: 80px 0;
}
.mint-box{
  max-width: 1300px;
  margin: 0 auto;
  .top-button{
    text-align: center;
    margin-bottom: 60px;
  }
  .title{
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    color: #fff;
    margin-bottom: 35px;
  }
  .info-nox{
    padding: 60px 120px;
    background: rgba(255, 255, 255, 0.10);
    border-radius: 20px;
    .info-title{
      text-align: center;
      color: #fff;
      font-size: 30px;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 3px;
      margin-bottom: 10px;
    }
    .desc{
       text-align: center;
      color: #fff;
      font-size: 18px;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 3px;
      margin-bottom: 60px;
    }
    .btn-box{
      text-transform: uppercase !important;
      margin-bottom: 24px;
    }
    .income-box{
      background: rgba(255,255,255,0.1);
      border-radius: 20px 20px 20px 20px;
      border: 2px solid rgba(255,255,255,0.3);
      padding: 30px 200px;
      .item{
        color: #fff;
        font-size: 16px;
        span{
          color: rgba(251, 22, 138, 1);
        }
      }
    }
  }
}
.my-nft-box{
  margin-top: 40px;
  .nft-title{
    color: #fff;
    font-size: 20px;
    font-weight: 700;
  }
  .nft-item{
    background: rgba(0,0,0,0.2);
    color: #fff;
    padding: 20px 30px;
    border-radius: 10px;
    margin-top: 15px;
  }
}
</style>

