		<div class="right">
      <div class="top">
        <div class="desc"><span>Created by:</span>PixelCase</div>
        <div class="title">PixelCase Collector</div>
      
      </div>
        <div class="center-box">
          <div class="todo-box">
            <div class="label-box flex-between">
              <div class="label">to do</div>
