              <div class="label">to do</div>
            </div>
             <div class="icon-box flex-middle">
               <div class="icon flex-middle">
                 <img src="@/assets/images/icon1.png" alt="">
               </div>
               <div class="line flex-item"></div>
               <div class="icon flex-middle">
                 <img src="@/assets/images/icon2.png" alt="">
               </div>
                <div class="line flex-item"></div>
               <div class="icon flex-middle" @click="mintNft">
                 <img src="@/assets/images/icon3.png" alt="">
               </div>
             </div>
             <div class="icon-texts flex-between">
               <div class="icon-text">Follow Twitter</div>
               <div class="icon-text">Like & RT</div>
               <div class="icon-text">Mint NFT</div>
             </div>
