          <div class="bottom-info">
            <div class="title">Silky NFT Empowerment</div>
            <div class="info-box">
               <div class="item flex-middle">
                  <i class="el-icon-arrow-right"></i>
                  <div class="desc">Hold Silky NFT to participate in the mining
pool to get$SKY tokens every day</div>
               </div>
               <div class="item flex-middle">
                    <i class="el-icon-arrow-right"></i>
                  <div class="desc">Early access to new features of the future
platform</div>
               </div>
               <div class="item flex-middle">
