<script>
import { reactive, toRefs, computed, watch } from "vue";
import { onBeforeRouteLeave } from "vue-router"
import { useStore } from "vuex";
import { ElMessage } from 'element-plus';
import config from "@/utils/config"
import nftAbi from "@/abi/nftAbi"
export default {
  components: {
  },
  setup () {
	 const store = useStore()
     const data = reactive({
	     address: computed(() => store.state.account),
       web3: computed(() => store.state.provider),
        initContract: () => {
        data.nftContract = new data.web3.eth.Contract(nftAbi, config.nftAddress)
      },
      mintNft: async () => {
         let isMint = await data.nftContract.methods.isMint(data.address).call()
         if(isMint) {
           ElMessage.error("Already Mint")
           return
         }
         try {
           let gas = await data.nftContract.methods.mint().estimateGas({from: data.address})
          await data.nftContract.methods.mint().send({ from: data.address, gas: gas });
          
          ElMessage.success("Mint Success")
         } catch (error) {
          ElMessage.error("Mint Fail")
           console.log(error)
         }
       },
     })
	 onBeforeRouteLeave(() => {
       window.document.body.classList.remove('bg')
	    window.document.getElementsByTagName('html')[0].classList.remove('bg');
	 })
	 window.document.body.classList.add('bg')
	  window.document.getElementsByTagName('html')[0].classList.add('bg');
    
     if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })

    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

