import Web3 from "web3";
import store from "../store/index"
import { ElMessage } from 'element-plus';
import { useI18n } from "vue-i18n"
import config from "./config";
import { connectorId } from '../storage'
const web3Data = {
  web3: {},
  t: null,
  factoryContract: {},
  web3Provider: {},
  getBlance: (account) => {
    web3Data.web3.eth.getBalance(account).then((amount) => {
      store.commit(
        "updateCurrentBalance",
        web3Data.web3.utils.fromWei(amount, "ether")
      );
    });
  },
  getNetId: async () => {
    let id = await web3Data.web3.eth.net.getId()
    if(config.chainId != id) {
      try {
        await window.ethereum.request({ method: 'wallet_switchEthereumChain', params: [{chainId: config.chainData.chainId }]})
      } catch (error) {
        
        await window.ethereum.request({ method: 'wallet_addEthereumChain', params: [config.chainData]})
        web3Data.getNetId()
      }
     
    } else {
      // store.commit(
      //   "updateNetId",
      //   id
      // );
    }  
  },
  initWeb3: () => {
    if (window.ethereum) {
      web3Data.web3Provider = window.ethereum;
      
      window.ethereum.on("accountsChanged", async (accounts) => {
        store.commit("updateAccount", accounts[0]);
      })
      window.ethereum.on('chainChanged', async () =>{
        await web3Data.getNetId()
        window.location.reload()
      });
      web3Data.web3 = new Web3(web3Data.web3Provider);
      window.ethereum.enable().then(async (data) => {
        console.log(data)
        store.commit("updateAccount", data[0]);
        await web3Data.getNetId()
      })
    } else {
      if (window.web3) {
        web3Data.web3Provider = window.web3.currentProvider;
        web3Data.web3 = new Web3(web3Data.web3Provider);
      } else {
        // if (!web3Data.web3.t) {
        //   web3Data.web3.t = useI18n().t
        // }
        ElMessage.error("No wallet plugin has been detected yet, please install the MetaMask wallet, or open it in the wallet DAPP browser")
      }
    }
    connectorId.set('injected')
    store.commit("updateProvider", web3Data.web3);
    web3Data.web3.eth.getAccounts().then(async (accounts) => {
      store.commit("updateAccount", accounts[0]);
      return web3Data.getBlance(accounts[0]);
    });
  }
}

export default web3Data;
