import { createRouter, createWebHashHistory } from "vue-router";
import Index from "../views/index";
import Mint from "../views/mint";

const routes = [
  {
    path: "/",
    name: "Index",
    component: Index
  },
  {
    path: "/mint",
    name: "Mint",
    component: Mint
  }
];

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

export default router;
